* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #1C1D1F;
}

.App {
  
  background-color: #1C1D1F;
  overflow: hidden;

}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1366px;
  width: 80%;
  height: 100vh;
  margin: 0 auto;
}

input {
  color: #C6C6C6;
  background-color: #18191B;
  padding: 10px 15px;
  border: none;
  outline: none;
  margin-bottom: 10px;
  border-radius: 5px;
}

button {
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
}

.flex {
  display: flex;
}

@media screen and (max-width: 1024px) {

  .flex {
    flex-direction: column;
  }

  .container {
    align-items: baseline;
    padding-top: 50px;
    height: max-content;
  }

  .App {
    overflow-y: visible;
  }

}

@media screen and (max-width: 520px) {
    .container {
      width: 90%;
    }
}